import React from 'react';

const Splash = () => {
  return (
    <div className="splash">
      <div>
        <h1>Wandering between two worlds</h1>
      </div>
      <div>
        <h1>One dead</h1>
      </div>
      <div>
        <h1>The other powerless to be born</h1>
      </div>
    </div>
  );
};

export default Splash;



